*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#heading
{
	margin-top: 20px;
	font-size: 80px;
	font-family: serif;
	text-shadow: 16px 20px 3px white;
	margin-bottom: 50px;
}
.btnquiz
{
	width: 170px;
	height: 170px;
	font-size: 40px;
	margin: 30px;
	cursor: pointer;
}
.btnquiz:hover{
	background: green;
}

#mywidth{
	width: 50%;
}
.quizquestion{
	
	border:2px solid black;
	border-radius: 5px;
	padding: 0px;
	margin-top: 20px;
	

}

.quizquestion h4
{
	text-align: center;
	font-size: 25px;
	width: 100%;
	background: #26114C;
	color: white;
}

.quizquestion button
{
	width: 80%;
	margin: 10px;
	cursor: pointer;
	font-size: 18px;
	border: none;
	outline: none;
	
	color: black;
	
}

.quizquestion button:hover
{
	border: 1px solid black;
	
}


.green{
	background: green;
	color: white;
}

.red{
	background: red;
	color: white;
}
.nextbtn
{
	width: 100px;
	height: 40px;
	color: white;
	background-color: black;
	border:none;
	cursor: pointer;
	margin-top: 20px;

}

.submitbtn
{
	width: 100px;
	height:40px;
	outline: none;
	color: white;
	background: black;
	margin-top: 20px;
	font-size: 20px;
	border:none;
}

.submitbtn:hover
{
	background-color:#C93734; 
}

@media(max-width: 768px)
{
	#mywidth{
	width: 70%;
}
}
